import React from "react"
import "../styles/header.css"
import { slide as Menu} from "react-burger-menu";
import { Link , graphql, useStaticQuery} from "gatsby"

export default function Header({data}) {
    const { site } = useStaticQuery(query);
    return (
        <header>
            <Menu width={250}>
                <span> --- Menu --- </span>
                <Link className="menu-item" to="/">Home</Link>
                <Link className="menu-item" to="/today_predict_forcus"></Link>
                <Link className="menu-item" to="/today_predict"></Link>
                {/* <Link className="menu-item" to="/past_forecasts"></Link> */}
                <Link className="menu-item" to="/about"></Link>
            </Menu>

            <p className="header-logo">{site.siteMetadata.siteSubTitle}</p> 
            <h1>{site.siteMetadata.siteName}</h1>
            <span className="kakugen"></span>
        </header>
    )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        siteName
        siteSubTitle
        siteDescription
        siteUrl
        siteOgpImage
        siteAuthor
      }
    }
  }
`